html, body {
  background-color: #222234 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  min-height: 100%;
}

.appcontainer {
  background-color: #282c34;
}

.thumbnails {
  padding-top: 2em;
  padding-bottom: 2em;
}

.thumbcontainer {
  padding-bottom: 1em;
 }

.cardbody {
  background-color: #474a51;
  font-size: small;
  padding-top: 12px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 30px;
  font-weight: 600;
  color: #ffffff;
}

.buttonContainer {
  align-content: center;
  text-align: center;
  padding: 2em;
}

.detailstitle {
  font-size: large;
  color: #ffffff;
}

.detailscontainer {
  align-content: center;
  text-align: center;
  padding: 2em;
}

.backbutton {
  margin-top: 2em;
}
